<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-button type="primary" v-on:click="getUser"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSaveUser('add')"
            ></el-button
          >
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <template>
      <el-table
        :data="users"
        highlight-current-row
        v-loading="loading"
        style="width: 100%"
      >
        <el-table-column type="index" label="" width="80"></el-table-column>

        <!-- width="120" -->
        <el-table-column
          prop="nickname"
          label=""
          sortable
        ></el-table-column>

        <!-- width="120" -->
        <el-table-column
          prop="username"
          label=""
          sortable
        ></el-table-column>
        <!-- width="120" -->
        <el-table-column
          prop="password"
          label=""
          sortable
        ></el-table-column>

        <!-- width="100" -->
        <!-- <el-table-column prop="sex" label="" sortable></el-table-column> -->

        <el-table-column prop="role" label="" sortable>
          <template slot-scope="scope">
            <span v-if="scope.row.role === 'ADMIN'"></span>
            <span v-if="scope.row.role === 'DEV'"></span>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="">
          <template slot-scope="scope">
            <el-switch
              on-text=""
              off-text=""
              v-model="scope.row.status"
              @change="handleSwitch(scope.row)"
            ></el-switch>
          </template>
        </el-table-column>
        <!-- <el-table-column prop="birth" label="" width="120" sortable></el-table-column> -->
        <!-- <el-table-column prop="addr" label="" min-width="180" sortable ></el-table-column> -->

        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <!-- <el-button
              size="small"
              @click="handleSaveUser('edit', scope.$index, scope.row)"
              ></el-button
            >
            <el-button
              type="danger"
              size="small"
              @click="handleDel(scope.$index, scope.row)"
              ></el-button
            > -->
            <el-button-group>
              <el-button
                size="mini"
                @click="handleSaveUser('edit', scope.$index, scope.row)"
                icon="el-icon-edit"
              ></el-button>
              <el-button
                type="danger"
                size="mini"
                @click="handleDel(scope.$index, scope.row)"
                icon="el-icon-delete"
              ></el-button
            ></el-button-group>
          </template>
        </el-table-column>
      </el-table>
    </template>

    <el-dialog
      :title="formTitle"
      v-if="formVisible"
      :visible.sync="formVisible"
      v-model="formVisible"
      :close-on-click-modal="false"
    >
      <!-- :rules="formRules" -->
      <el-form
        :model="formArgs"
        :rules="formRules"
        label-width="80px"
        ref="formArgs"
        style="margin-right: 30px"
      >
        <el-form-item label="" prop="nickname">
          <el-input
            v-model="formArgs.nickname"
            auto-complete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="username">
          <el-input
            v-model="formArgs.username"
            auto-complete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            v-model="formArgs.password"
            auto-complete="off"
            placeholder=""
          ></el-input>
        </el-form-item>

        <!-- style="margin-left: 20px"  collapse-tags-->
        <el-form-item label="">
          <el-select
            style="width: 100%"
            v-model="formArgs.hosts"
            multiple
            placeholder=""
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="role">
          <el-radio-group v-model="formArgs.role">
            <el-radio label="ADMIN" v-model="formArgs.role"></el-radio>
            <el-radio label="DEV" v-model="formArgs.role"></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="" prop="status">
          <el-switch
            on-text=""
            off-text=""
            v-model="formArgs.status"
          ></el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click.native="formVisible = false"></el-button>
        <el-button type="primary" @click.native="saveBtn" :loading="saveLoading"
          ></el-button
        >
      </div>
    </el-dialog>
  </section>
</template>
<script>
import {
  getUserList,
  getRedisConfigSelect,
  saveUser,
  deleteUser,
} from "../../api/api";
//import NProgress from 'nprogress'
export default {
  data() {
    return {
      filters: {
        name: "",
      },
      loading: false,
      users: [],
      // flag
      formVisible: false,
      formTitle: "",
      formArgs: {
        nickname: "",
        username: "",
        password: "",
        status: false,
        role: "DEV",
      },
      formRules: {
        nickname: [
          { required: true, message: "", trigger: "blur" },
        ],
        username: [{ required: true, message: "", trigger: "blur" }],
        password: [{ required: true, message: "", trigger: "blur" }],
      },
      saveLoading: false,
      options: [
        // {
        //   value: "127.0.0.1:6379",
        //   label: "-1",
        // },
        // {
        //   value: "127.0.0.2:6379",
        //   label: "-2",
        // },
        // {
        //   value: "127.0.0.3:6379",
        //   label: "-3",
        // },
        // {
        //   value: "127.0.0.4:6379",
        //   label: "-4",
        // },
        // {
        //   value: "127.0.0.5:6379",
        //   label: "-5",
        // },
        // {
        //   value: "127.0.0.6:6379",
        //   label: "-6",
        // },
        // {
        //   value: "127.0.0.7:6379",
        //   label: "-7",
        // },
        // {
        //   value: "127.0.0.8:6379",
        //   label: "-8",
        // },
        // {
        //   value: "127.0.0.9:6379",
        //   label: "-9",
        // },
        // {
        //   value: "127.0.0.10:6379",
        //   label: "-10",
        // },
        // {
        //   value: "127.0.0.11:6379",
        //   label: "-11",
        // },
      ],
      // selectValues: [],
      // value1: ["127.0.0.1:6379", "127.0.0.2:6379"],
    };
  },
  methods: {
    // 
    handleDel(index, row) {
      this.$confirm("?", "", {
        type: "warning",
      })
        .then(() => {
          console.log("row", row);
          this.loading = true;

          deleteUser([row.id]).then((res) => {
            this.loading = false;
            this.getUser();
          });
        })
        .catch(() => {});
    },
    //  
    saveBtn() {
      // console.log(this.formArgs);
      // console.log(this.selectValues);
      var _reqdata = { ...this.formArgs };
      // console.log("reqdata", _reqdata);
      this.saveLoading = true;
      saveUser(_reqdata).then((res) => {
        this.saveLoading = false;
        this.$message({
          message: res.msg,
          type: res.code == 200 ? "success" : "error",
        });
        this.getUser();
        this.formVisible = false;
      });
    },
    //
    formatSex: function (row, column) {
      return row.sex == 1 ? "" : row.sex == 0 ? "" : "";
    },
    //
    getUser() {
      // let para = {
      // 	name: this.filters.name
      // };
      this.loading = true;
      //NProgress.start();
      getUserList().then((res) => {
        this.loading = false;

        if (res.code === 200) {
          this.users = res.data;
        }
        //NProgress.done();
      });
    },
    // 
    handleSaveUser(type, index, row) {
      console.log("type", type);
      console.log("index", index);
      console.log("row", row);

      this.formTitle = type == "add" ? "" : "";
      this.formVisible = true;
      if ("edit" == type) {
        this.formArgs = row;
      } else {
        this.formArgs = {};
      }
    },
    // redis
    loadRedisConfig() {
      getRedisConfigSelect().then((res) => {
        if (res.code === 200) {
          res.data.forEach((item) => {
            var _value = item.host + ":" + item.port;
            this.options.push({
              value: _value,
              label: item.name,
            });
          });
        }
      });
    },
  },
  mounted() {
    // 
    this.getUser();
    // redis
    this.loadRedisConfig();
  },
};
</script>

<style scoped>
</style>


