<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px">
      <el-form :inline="true" :model="filters">
        <!-- <el-form-item>
          <el-input v-model="filters.name" placeholder=""></el-input>
        </el-form-item>-->
        <el-form-item>
          <el-button type="primary" v-on:click="getRedisConfigs"
            ></el-button
          >
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table
      :data="redisConfigs"
      highlight-current-row
      v-loading="listLoading"
      element-loading-text=". . ."
      @selection-change="selsChange"
      style="width: 100%"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="host" label="IP"></el-table-column>
      <el-table-column prop="port" label=""></el-table-column>
      <!-- <el-table-column prop="password" label=""></el-table-column> -->
      <el-table-column prop="env" label="">
        <template slot-scope="scope">
          <span v-html="formatterEnv(scope.row.env)"></span>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="">
        <template slot-scope="scope">
          <el-switch
            on-text=""
            off-text=""
            v-model="scope.row.status"
            @change="handleSwitch(scope.row)"
          ></el-switch>
        </template>
      </el-table-column>

      <!-- <el-table-column prop="isHook" label="">
        <template slot-scope="scope">
          <el-switch
            on-text=""
            off-text=""
            v-model="scope.row.isHook"
            @change="handleSwitchHook(scope.row)"
          ></el-switch>
        </template>
      </el-table-column> -->

      <el-table-column label="" width="150">
        <template slot-scope="scope">
          <!-- <el-button size="small" @click="handleEdit(scope.$index, scope.row)"
            ></el-button
          >
          <el-button
            type="danger"
            size="small"
            @click="handleDel(scope.$index, scope.row)"
            ></el-button> -->

          <el-button-group>
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)" icon="el-icon-edit"
              ></el-button
            >
            <el-button
              type="danger"
              size="mini"
              @click="handleDel(scope.$index, scope.row)" icon="el-icon-delete"
              ></el-button
            ></el-button-group>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button> -->
      <el-pagination
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size="20"
        :total="total"
        style="float: right"
      ></el-pagination>
    </el-col>

    <!---->
    <el-dialog
      title=""
      v-model="editFormVisible"
      v-if="editFormVisible"
      :visible.sync="editFormVisible"
      :close-on-click-modal="false"
    >
      <el-form
        :model="editForm"
        label-width="80px"
        :rules="editFormRules"
        ref="editForm"
      >
        <el-form-item label="" prop="name">
          <el-input
            v-model="editForm.name"
            auto-complete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="IP" prop="host">
          <el-input
            v-model="editForm.host"
            auto-complete="off"
            placeholder="IP"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="port">
          <el-input
            type="number"
            v-model="editForm.port"
            auto-complete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            v-model="editForm.password"
            auto-complete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="editForm.env">
            <!-- <el-radio label="" name="prod"></el-radio>
            <el-radio label="" name="test"></el-radio>
            <el-radio label="" name="dev"></el-radio>
            <el-radio label="" name="local"></el-radio>-->
            <el-radio
              v-for="item in envs"
              :key="item.code"
              :value="item.code"
              :label="item.code"
              >{{ item.name }}</el-radio
            >
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-switch
            on-text=""
            off-text=""
            v-model="editForm.status"
          ></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button
          type="primary"
          @click.native="testConnSubmit2"
          :loading="testConnLoading"
          ></el-button
        >
        <el-button
          type="primary"
          @click.native="editSubmit"
          :loading="editLoading"
          ></el-button
        >
      </div>
    </el-dialog>

    <!---->
    <el-dialog
      title=""
      v-if="addFormVisible"
      :visible.sync="addFormVisible"
      v-model="addFormVisible"
      :close-on-click-modal="false"
    >
      <el-form
        :model="addForm"
        label-width="80px"
        :rules="addFormRules"
        ref="addForm"
      >
        <el-form-item label="" prop="name">
          <el-input
            v-model="addForm.name"
            auto-complete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="IP" prop="host">
          <el-input
            v-model="addForm.host"
            auto-complete="off"
            placeholder="IP"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="port">
          <el-input
            type="number"
            v-model="addForm.port"
            auto-complete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            v-model="addForm.password"
            auto-complete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="addForm.env">
            <!-- <el-radio label="" name="prod"></el-radio>
            <el-radio label="" name="test"></el-radio>
            <el-radio label="" name="dev"></el-radio>
            <el-radio label="" name="local"></el-radio>-->
            <el-radio
              v-for="item in envs"
              :key="item.code"
              :value="item.code"
              :label="item.code"
              >{{ item.name }}</el-radio
            >
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-switch
            on-text=""
            off-text=""
            v-model="addForm.status"
          ></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button
          type="primary"
          @click.native="testConnSubmit"
          :loading="testConnLoading"
          ></el-button
        >
        <el-button
          type="primary"
          @click.native="addSubmit"
          :loading="addLoading"
          ></el-button
        >
      </div>
    </el-dialog>
  </section>
</template>

<script>
import util from "../../common/js/util";
//import NProgress from 'nprogress'
import {
  getRedisConfigList,
  addRedisConfig,
  editRedisConfig,
  enableRedisConfig,
  disableRedisConfig,
  deleteRedisConfig,
  testConnRedisConfig,
  redisEnabledHook,
  redisDisabledHook,
} from "../../api/api";

export default {
  data() {
    return {
      filters: {
        name: "",
      },
      envs: [
        {
          code: "PROD",
          name: "",
        },
        {
          code: "TEST",
          name: "",
        },
        {
          code: "DEV",
          name: "",
        },
        {
          code: "LOCAL",
          name: "",
        },
      ],
      redisConfigs: [],
      total: 0,
      page: 1,
      listLoading: false,
      sels: [], //
      editFormVisible: false, //
      editLoading: false,
      editFormRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        host: [{ required: true, message: "IP", trigger: "blur" }],
        port: [
          {
            //type: "number",
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
      //
      editForm: {
        name: "",
        host: "",
        port: "",
        password: "",
        env: "",
        status: true,
      },
      addFormVisible: false, //
      addLoading: false,
      testConnLoading: false,
      addFormRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        host: [{ required: true, message: "IP", trigger: "blur" }],
        port: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
      //
      addForm: {
        name: "",
        host: "",
        port: 6379,
        password: "",
        env: "local",
        status: true,
      },
    };
  },
  created() {
    this.getRedisConfigs();
  },
  methods: {
    // 
    formatterEnv: function (val) {
      if (val === "PROD") {
        return "<font color='#009688'></font>";
      }
      if (val === "TEST") {
        return "<font color='#FF5722'></font>";
      }
      if (val === "DEV") {
        return "<font color='#1E9FFF'></font>";
      }
      if (val === "LOCAL") {
        return "<font color='#393D49'></font>";
      }
    },
    handleCurrentChange(val) {
      this.page = val;
      this.getUsers();
    },
    getRedisConfigs() {
      this.listLoading = true;
      getRedisConfigList().then((res) => {
        this.listLoading = false;

        // console.log(res);

        this.redisConfigs = res.data;
      });
    },
    handleSwitch(row) {
      if (row.status) {
        enableRedisConfig({ host: row.host, port: row.port })
          .then((res) => {
            this.listLoading = false;
            if (res.code == 200) {
              this.$message({
                message: res.msg,
                type: "success",
              });
            } else {
              row.status = !row.status;
              this.$message({
                message: res.msg,
                type: "error",
              });
            }
          })
          .catch((err) => {
            this.$message({
              message: err.msg,
              type: "error",
            });
          });
      } else {
        disableRedisConfig({ host: row.host, port: row.port })
          .then((res) => {
            this.listLoading = false;
            if (res.code == 200) {
              this.$message({
                message: res.msg,
                type: "success",
              });
            } else {
              row.status = !row.status;
              this.$message({
                message: res.msg,
                type: "error",
              });
            }
          })
          .catch((err) => {
            this.$message({
              message: err.msg,
              type: "error",
            });
          });
      }
    },
    //
    handleDel: function (index, row) {
      this.$confirm("?", "", {
        type: "warning",
      })
        .then(() => {
          console.log("row", row);
          this.listLoading = true;

          deleteRedisConfig({ host: row.host, port: row.port }).then((res) => {
            this.listLoading = false;
            this.$message({
              message: res.msg,
              type: res.code == 200 ? "success" : "error",
            });

            this.getRedisConfigs();
          });
        })
        .catch(() => {});
    },
    //
    handleEdit: function (index, row) {
      // console.log(row)
      this.editFormVisible = true;
      this.editForm = Object.assign({}, row);
    },
    //
    handleAdd: function () {
      this.addFormVisible = true;
      this.addForm = {
        name: "",
        host: "",
        port: 6379,
        password: "",
        env: "LOCAL",
        status: true,
      };
    },
    //
    editSubmit: function () {
      this.$refs.editForm.validate((valid) => {
        // console.log(valid)
        if (valid) {
          this.$confirm("", "", { type: "warning" }).then(
            () => {
              this.editLoading = true;
              //NProgress.start();
              let param = Object.assign({}, this.editForm);
              console.log(param);
              editRedisConfig(param).then((res) => {
                this.editLoading = false;
                //NProgress.done();
                this.$message({
                  message: res.msg,
                  type: res.code == 200 ? "success" : "error",
                });
                //    
                if (res.code == 200) {
                  this.$refs["editForm"].resetFields();
                  this.editFormVisible = false;
                  this.getRedisConfigs();
                }
              });
            }
          );
        }
      });
    },
    //
    addSubmit: function () {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          this.$confirm("", "", { type: "warning" }).then(
            () => {
              // this.addLoading = true;
              let param = Object.assign({}, this.addForm);
              // console.log(param);

              addRedisConfig(param).then((res) => {
                this.addLoading = false;

                this.$message({
                  message: res.msg,
                  type: res.code == 200 ? "success" : "error",
                });

                if (res.code == 200) {
                  // 
                  this.$refs["addForm"].resetFields();
                  this.addFormVisible = false;
                  this.getRedisConfigs();
                }
              });
            }
          );
        }
      });
    },
    // 
    testConnSubmit2: function () {
      this.$refs.editForm.validate((valid) => {
        if (valid) {
          this.testConnLoading = true;
          let param = Object.assign({}, this.editForm);

          testConnRedisConfig(param).then((res) => {
            this.testConnLoading = false;

            this.$message({
              message: res.msg,
              type: res.code == 200 ? "success" : "error",
            });
          });
        }
      });
    },
    // 
    testConnSubmit: function () {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          this.testConnLoading = true;
          let param = Object.assign({}, this.addForm);

          testConnRedisConfig(param).then((res) => {
            this.testConnLoading = false;

            this.$message({
              message: res.msg,
              type: res.code == 200 ? "success" : "error",
            });
          });
        }
      });
    },
    selsChange: function (sels) {
      this.sels = sels;
    },
    //
    batchRemove: function () {
      var ids = this.sels.map((item) => item.id).toString();
      this.$confirm("", "", {
        type: "warning",
      })
        .then(() => {
          this.listLoading = true;
          //NProgress.start();
          let para = { ids: ids };
          batchRemoveUser(para).then((res) => {
            this.listLoading = false;
            //NProgress.done();
            this.$message({
              message: "",
              type: "success",
            });
            this.getUsers();
          });
        })
        .catch(() => {});
    },
    handleSwitchHook: function (row) {
      // alert(row.isHook);

      if (row.isHook) {
        redisEnabledHook(row.host)
          .then((res) => {
            this.listLoading = false;
            if (res.code == 200) {
              this.$message({
                message: res.msg,
                type: "success",
              });
            } else {
              row.status = !row.status;
              this.$message({
                message: res.msg,
                type: "error",
              });
            }
          })
          .catch((err) => {
            this.$message({
              message: err.msg,
              type: "error",
            });
          });
      } else {
        redisDisabledHook(row.host)
          .then((res) => {
            this.listLoading = false;
            if (res.code == 200) {
              this.$message({
                message: res.msg,
                type: "success",
              });
            } else {
              row.status = !row.status;
              this.$message({
                message: res.msg,
                type: "error",
              });
            }
          })
          .catch((err) => {
            this.$message({
              message: err.msg,
              type: "error",
            });
          });
      }
    },
  },
  mounted() {
    // this.getRedisConfigs();
  },
};
</script>

<style scoped>
</style>


