<template>
  <section>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form ref="queryForm" :inline="true" :model="queryForm">
        <el-form-item label="">
          <el-input
            size="medium"
            v-model="queryForm.keyword"
            placeholder=""
            style="width:300px"
          ></el-input>
        </el-form-item>

        <el-form-item>
          <el-button size="medium" type="primary" @click="onSubmitQuery"></el-button>
          <!-- <el-button size="medium"></el-button> -->
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-col :span="24" class="toolbar">
      <!--  -->
      <el-button size="medium" type="primary" @click="handleAdd2"></el-button>

      <el-button size="medium" type="primary" plain @click="handleTaskList"></el-button>
    </el-col>

    <!--   -->
    <el-table
      :data="taskData"
      v-loading="taskDataLoading"
      element-loading-text=". . ."
      style="width: 100%"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column fixed prop="name" label=""></el-table-column>
      <el-table-column prop="available" label="" width="180">
        <template slot-scope="scope">
          <el-switch v-model="scope.row.available" @change="handleSwitch(scope.row)"></el-switch>
        </template>
      </el-table-column>
      <el-table-column prop="configCount" label="" width="180">
        <template slot-scope="scope">
          <span>{{scope.row.configCount}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="120">
        <template slot-scope="scope">
          <el-button-group>
            <!--  -->
            <el-button
              size="mini"
              icon="el-icon-edit"
              @click="handleEditGroup(scope.$index, scope.row)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              size="mini"
              icon="el-icon-delete"
              @click="handleGroupDelete(scope.row)"
            ></el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      v-if="addFormVisible"
      v-loading="addFormLoading"
      element-loading-text=". . ."
      :visible.sync="addFormVisible"
      @close="cancelAdd"
      :disabled="true"
      width="70%"
    >
      <span slot="title" class="dialog-footer">
        <span style="font-size:20px">{{dialogTitle}}</span>
        <span>{{dialogDesc}}</span>
        <span
          v-loading="dialogLoading"
          element-loading-spinner="el-icon-loading"
          style="top: 13px;left: 8px;"
        ></span>
      </span>

      <el-form ref="addForm" :model="addForm" label-width="100px">
        <el-form-item label="" required :error="nameError">
          <el-input
            v-model="addForm.name"
            placeholder=""
            style="width:60%"
            @blur="handleVerifyBlur('oneName')"
          ></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input type="number" v-model="addForm.level" value="0" style="width:60%"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-select v-model="addForm.type" placeholder="" style="width:60%">
            <el-option label="" value="-1"></el-option>
            <el-option label="HTTP" value="httpRequest"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item id="transfer1" label="">
          <!-- :filter-method="filterTransfer1" -->
          <el-transfer
            v-model="value"
            :data="data1"
            filterable
            filter-placeholder="http"
            :titles="['', '']"
            class="custom-transfer1"
            v-loading="transfer1Loading"
            style="line-height: 0px"
          ></el-transfer>
        </el-form-item>

        <!-- <el-form-item label=""></el-form-item> -->
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button type="primary" @click.native="addFormSubmit" :loading="addFormLoading"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      v-if="addFormVisible2"
      v-loading="addFormLoading2"
      element-loading-text=". . ."
      :visible.sync="addFormVisible2"
      @close="cancelAdd2"
      :disabled="true"
      width="70%"
    >
      <span slot="title" class="dialog-footer">
        <span style="font-size:20px">{{dialogTitle2}}</span>
        <span>{{dialogDesc2}}</span>
        <span
          v-loading="dialog2Loading"
          element-loading-spinner="el-icon-loading"
          style="top: 13px;left: 8px;"
        ></span>
      </span>

      <el-form ref="addForm2" :model="addForm2" label-width="100px">
        <el-form-item label="" required :error="groupNameError">
          <el-input
            v-model="addForm2.name"
            placeholder=""
            style="width:60%"
            @blur="handleVerifyBlur('groupName')"
          ></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-switch v-model="addForm2.available"></el-switch>
        </el-form-item>

        <el-form-item label="">
          <el-transfer
            v-model="value"
            :data="data2"
            filterable
            filter-placeholder=""
            :titles="['', '']"
            v-loading="transfer2Loading"
            class="custom-transfer2"
            style="line-height: 0px"
          ></el-transfer>
        </el-form-item>

        <!-- <el-form-item label=""></el-form-item> -->
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible2 = false"></el-button>
        <el-button type="primary" @click.native="addFormSubmit2" :loading="addFormLoading2"></el-button>
      </div>
    </el-dialog>

    <!-- form -->
    <el-dialog
      v-if="taskListFormVisible"
      v-loading="taskListFormLoading"
      element-loading-text=". . ."
      :visible.sync="taskListFormVisible"
      @close="cancelTaskList"
      :disabled="true"
      width="80%"
    >
      <span slot="title" class="dialog-footer">
        <span style="font-size:20px">{{dialogTitle3}}</span>
        <span>{{dialogDesc3}}</span>
      </span>

      <el-form ref="queryTaskOneForm" :inline="true" :model="queryTaskOneForm">
        <el-form-item label="">
          <el-input
            size="medium"
            v-model="queryTaskOneForm.keyword"
            placeholder=""
            style="width:300px"
          ></el-input>
        </el-form-item>

        <el-form-item>
          <el-button size="medium" type="primary" @click="onSubmitQueryTaskOne"></el-button>
          <el-button size="medium" type="primary" plain @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>

      <!--   -->
      <el-table
        :data="taskOneData"
        v-loading="taskOneDataLoading"
        element-loading-text=". . ."
        style="width: 100%"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column fixed prop="name" label=""></el-table-column>
        <el-table-column prop="configCount" label="/" width="180"></el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <el-button-group>
              <!--  -->
              <el-button
                size="mini"
                icon="el-icon-edit"
                @click="handleEdit(scope.$index, scope.row)"
              ></el-button>
              <!--  -->
              <el-button
                type="danger"
                size="mini"
                icon="el-icon-delete"
                @click="handleOneDelete(scope.row)"
              ></el-button>
              <!-- <el-button type="primary" size="mini" @click="handleSynch(scope.$index, scope.row)">Send</el-button> -->
            </el-button-group>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </section>
</template>

<script>
import {
  httpclientSelect,
  redisTaskSaveOne,
  redisTaskDeleteOne,
  redisTaskSelect,
  redisTaskGroupSave,
  redisTaskGroupDelete,
  redisTaskGroupList,
  redisTaskOneList
} from "../../api/api";

export default {
  data() {
    // const generateData = _ => {
    // const data = [];
    // const cities = ["", "", "", "", "", "", ""];
    // const pinyin = [
    //   "shanghai",
    //   "beijing",
    //   "guangzhou",
    //   "shenzhen",
    //   "nanjing",
    //   "xian",
    //   "chengdu"
    // ];
    // cities.forEach((city, index) => {
    //   data.push({
    //     label: city,
    //     key: index,
    //     pinyin: pinyin[index]
    //   });
    // });

    // return data;
    // };
    return {
      // data1: generateData(),
      transfer1Loading: false,
      transfer2Loading: false,
      groupNameError: "",
      nameError: "",
      data1: [],
      data2: [],
      value: [],
      taskData: [],
      taskOneData: [],
      taskOneDataLoading: false,
      taskListFormVisible: false,
      taskListFormLoading: false,
      taskDataLoading: false,
      addFormLoading: false,
      addFormLoading2: false,
      dialogTitle: "",
      dialogDesc: "",
      addFormVisible: false,
      dialogTitle2: "",
      dialogDesc2: "",
      dialogDesc3: "",
      dialogLoading: false,
      dialog2Loading: false,
      addFormVisible2: false,
      addForm: {},
      addForm2: {},
      queryForm: {
        keyword: ""
      },
      queryTaskOneForm: {
        keyword: ""
      }
    };
  },
  mounted() {
    this.loadTaskList();
  },
  methods: {
    sumConfig: function(val) {
      return val != null && val.length > 0 ? val.length : 0;
    },
    loadTaskList: function() {
      let reqData = {
        keyword: this.queryForm.keyword
      };

      this.taskDataLoading = true;
      redisTaskGroupList(reqData).then(res => {
        console.log(res);
        this.taskDataLoading = false;
        if (res.code === 200) {
          this.taskData = res.data;
        }
      });
    },
    loadTaskOneList: function() {
      this.dialogTitle3 = "";
      this.dialogDesc3 = "";

      this.taskListFormVisible = true;

      let reqData = {
        keyword: this.queryTaskOneForm.keyword
      };

      this.taskOneDataLoading = true;

      redisTaskOneList(reqData).then(res => {
        this.taskOneDataLoading = false;

        if (res.code === 200) {
          this.taskOneData = res.data;
        }
      });
    },
    onSubmitQuery: function() {
      this.loadTaskList();
    },
    onSubmitQueryTaskOne: function() {
      this.loadTaskOneList();
    },
    // 
    handleAdd: function() {
      this.dialogTitle = "";
      this.dialogDesc = "";
      this.addFormVisible = true;

      this.transfer1Loading = true;
      httpclientSelect({ qt: "all" }).then(res => {
        this.transfer1Loading = false;
        console.log(res);
        if (res.code === 200) {
          let object = res.data;
          for (const key in object) {
            this.data1.push({
              label: object[key],
              key: key
            });
          }
        }
      });
    },
    // 
    cancelAdd: function() {
      this.data1 = [];
      this.data2 = [];
      this.value = [];
      this.addForm = {};
      this.nameError = "";
    },
    // 2
    handleAdd2: function() {
      this.dialogTitle2 = "";
      this.dialogDesc2 = "";
      this.addFormVisible2 = true;

      let reqData = {
        keyword: "RA:TASK_CONFIG"
      };

      this.transfer2Loading = true;
      redisTaskSelect(reqData).then(res => {
        this.transfer2Loading = false;
        if (res.code === 200) {
          let object = res.data;
          for (const key in object) {
            this.data2.push({
              label: object[key],
              key: key
            });
          }
        }
      });
    },
    // 2
    cancelAdd2: function() {
      this.data2 = [];
      this.value = [];
      this.groupNameError = "";
      this.addForm = {};
    },
    addFormSubmit: function() {
      // console.log(this.value);
      // console.log(this.addForm);

      let typeAlias = "";
      if (this.addForm.type === "httpRequest") {
        typeAlias = "HTTP";
      }

      if (!this.addForm.name) {
        this.handleVerifyBlur("oneName");
        return false;
      }

      let reqData = {
        name: this.addForm.name,
        type: this.addForm.type,
        typeAlias: typeAlias,
        level: this.addForm.level,
        config: this.value
      };

      console.log(reqData);

      redisTaskSaveOne(reqData).then(res => {
        this.$notify({
          title: "",
          message: res.msg,
          type: res.code === 200 ? "success" : "error"
        });

        if (res.code === 200) {
          this.addFormVisible = false;
          this.loadTaskOneList();
        }
      });
    },
    addFormSubmit2: function() {
      if (!this.addForm2.name) {
        this.handleVerifyBlur("groupName");
        return false;
      }

      let reqData = {
        name: this.addForm2.name,
        available: this.addForm2.available,
        config: this.value
      };

      console.log(reqData);

      // return ;

      redisTaskGroupSave(reqData).then(res => {
        this.$notify({
          title: "",
          message: res.msg,
          type: res.code === 200 ? "success" : "error"
        });

        if (res.code === 200) {
          this.addFormVisible2 = false;
          this.loadTaskList();
        }
      });
    },
    handleSwitch: function(row) {},
    // 
    handleEdit: function(index, row) {
      this.addFormVisible = true;
      this.dialogTitle = "";
      this.dialogDesc = "";

      this.dialogLoading = true;

      console.log(row);
      this.addForm = row;

      this.transfer1Loading = true;
      httpclientSelect({
        host: row.host,
        db: row.db,
        qt: "all"
      }).then(res => {
        this.dialogLoading = false;
        this.transfer1Loading = false;

        console.log(res);
        if (res.code === 200) {
          let object = res.data;
          for (const key in object) {
            this.data1.push({
              label: object[key],
              key: key
            });
          }
        }
      });

      this.value = row.config;
    },
    // 
    handleEditGroup: function(index, row) {
      console.log(row);

      this.addFormVisible2 = true;
      this.dialogTitle2 = "";
      this.dialogDesc2 = "";
      this.dialog2Loading = true;

      this.addForm2 = {
        name: row.name,
        available: row.available
      };

      this.transfer2Loading = true;
      redisTaskSelect({
        host: row.host,
        db: row.db,
        keyword: "RA:TASK_CONFIG"
      }).then(res => {
        this.dialog2Loading = false;
        this.transfer2Loading = false;
        if (res.code === 200) {
          let object = res.data;
          for (const key in object) {
            this.data2.push({
              label: object[key],
              key: key
            });
          }
        }
      });

      this.value = row.config;
    },
    handleTaskList: function() {
      this.loadTaskOneList();
    },
    cancelTaskList: function() {
      this.taskListFormVisible = false;
      this.value = [];
    },
    handleVerifyBlur: function(name) {
      if (name === "groupName") {
        this.groupNameError = !this.addForm2.name ? "" : "";
      }
      if (name === "oneName") {
        this.nameError = !this.addForm.name ? "" : "";
      }
    },
    handleGroupDelete: function(row) {
      // console.log(row);

      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          redisTaskGroupDelete(row).then(res => {
            console.log(res);

            this.$notify({
              title: "",
              message: res.msg,
              type: res.code == 200 ? "success" : "error"
            });

            if (res.code === 200) {
              this.loadTaskList();
            }
          });
        })
        .catch(() => {});
    },
    handleOneDelete: function(row) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          redisTaskDeleteOne(row).then(res => {
            console.log(res);

            this.$notify({
              title: "",
              message: res.msg,
              type: res.code == 200 ? "success" : "error"
            });

            if (res.code === 200) {
              this.addFormVisible = false;
              this.loadTaskOneList();
            }
          });
        })
        .catch(() => {});
    },
    filterTransfer1: function(query, item) {
      console.log(query);
      console.log(query);
    }
  }
};
</script>

<style>
.custom-transfer1 .el-transfer-panel__item.el-checkbox .el-checkbox__label,
.custom-transfer2 .el-transfer-panel__item.el-checkbox .el-checkbox__label {
  overflow: visible;
  font-size: 12px;
}
.custom-transfer1 .el-transfer-panel,
.custom-transfer2 .el-transfer-panel {
  width: 260px;
}
</style>


